<template>
  <div class="home">
    <el-card shadow="never">
      <div class="copy-funtion">
        <div>
          <el-button plain @click="SearchTime(1)">今天</el-button>
          <el-button plain @click="SearchTime(2)">昨日</el-button>
          <el-button plain @click="SearchTime(3)">最近七天</el-button>
          <el-button plain @click="SearchTime(4)">最近30天</el-button>
          <el-date-picker
            class="time"
            v-model="SelTime"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="SearchTime(5)"
          ></el-date-picker>
        </div>
        <div class="copy-funtion">
          <el-button class="dowload" @click="dow=true">
            下载报表
            <i class="el-icon-download el-icon--right"></i>
          </el-button>
          <el-input placeholder="请输入内容" v-model="search" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="Search()"></el-button>
          </el-input>
        </div>
      </div>
    </el-card>
    <el-card shadow="never" class="copy-center">
      <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{prop: 'date', order: 'descending'}"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column type="expand" width="55">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="访问来源">
                <span>：{{ props.row.data.url }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="data.url" align="center" label="访问来源" width="430"></el-table-column>
        <el-table-column
          prop="created_at"
          align="center"
          label="复制时间"
          sortable
          :formatter="formatterTime"
          width="180"
        ></el-table-column>
        <el-table-column prop="data.ip" align="center" label="复制IP"></el-table-column>
        <el-table-column
          prop="data.address"
          align="center"
          label="用户位置"

        ></el-table-column>
                  <!-- :formatter="formatterAddress" -->
        <el-table-column prop="data.search" align="center" label="用户搜索词"></el-table-column>
        <el-table-column prop="data.wechar" align="center" label="复制微信"></el-table-column>

        <el-table-column align="center" label="用户标签" width="400">
          <template slot-scope="props">
            <el-tag v-if="props.row.data.isiPad">iPad</el-tag>
            <el-tag v-else-if="props.row.data.iPhone">iPhone</el-tag>
            <el-tag v-else-if="props.row.data.isAndroidTablet">平板手机</el-tag>
            <el-tag v-else-if="props.row.data.isAndroid">Android</el-tag>

            <el-tag v-if="props.row.data.isWindows">Window</el-tag>
            <el-tag v-else-if="props.row.data.isLinux">Linux</el-tag>
            <el-tag v-else-if="props.row.data.isTablet">平板电脑</el-tag>
            <el-tag v-else-if="props.row.data.isMac">苹果电脑</el-tag>

            <!-- <el-tag type="warning">标签四</el-tag>
            <el-tag type="danger">标签五</el-tag>-->
            <!-- <el-tag type="success">成功复制</el-tag> -->
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-card shadow="never" class="user-list-utils">
      <div class="user-list-utils-flex">
        <div>
          <el-button type="danger" plain @click="DelCopy()">删除复制</el-button>
        </div>
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.currentPage"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="this.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.count"
          ></el-pagination>
        </div>
      </div>
    </el-card>

    <el-dialog title="下载报表" :visible.sync="dow">
      <el-form ref="form" label-width="80px">
        <el-form-item label="时间选择">
          <el-date-picker
            v-model="dowTime"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
        </el-form-item>

        <el-form-item label="下载条数">
          <el-col :span="4">
            <el-input
              placeholder="下载条数"
              v-model="dowNum"
              type="number"
              @change="dowNum>0?dowNum:dowNum=1"
            ></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="包含链接">
          <el-col :span="8">
            <el-input
              placeholder="下载链接"
              v-model="dowUrl"
              type="text"
            ></el-input>
          </el-col>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dow = false">取 消</el-button>
        <el-button type="primary" @click="download()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Vue from "vue";
import Component from "vue-class-component";
import moment from "moment";
import excel from "excel-export";
import sever from "file-saver";

@Component
export default class copy extends Vue {
  time = {
    start: 0,
    end: moment()
  };
  SelTime = [];
  search = "";

  // 当前条数
  count = 0;
  // 数据
  tableData = [];
  // 当前一页大小
  pageSize = 10;
  // 在第几页
  currentPage = 1;

  // 选择删除数据
  multipleSelection = [];

  // 下载
  dow = false;
  dowTime = [new Date() - 60 * 60 * 24 * 30 * 1000, new Date()];
  dowNum = 1000;
  dowUrl = "";

  created() {
    this.getCopy();
  }

  // 获取表
  getCopy(pageSize = 10, currentPage = 1, search = "", time) {
    this.$api
      .GteCopy({
        params: {
          pageSize,
          currentPage,
          search,
          time
        }
      })
      .then(v => {
        const { count, rows } = v.data;
        this.count = count;
        this.tableData = rows;
      });
  }
  // 过滤
  formatterTime(row) {
    return moment(row.created_at).format("YYYY-MM-DD, HH:mm:ss");
  }
  // formatterAddress(row) {
  //   return row.data.regionName + "" + row.data.city;
  // }
  // 页码
  handleSizeChange(val) {
    this.pageSize = val;
    this.getCopy(val, this.currentPage, this.search, this.time);
  }
  handleCurrentChange(val) {
    this.currentPage = val;
    this.getCopy(this.pageSize, val, this.search, this.time);
  }
  // 搜索词时间
  Search() {
    this.getCopy(this.pageSize, this.currentPage, this.search, this.time);
  }
  SearchTime(val) {
    switch (val) {
      case 1:
        this.time.start = moment().startOf("day");
        this.time.end = moment();
        break;
      case 2:
        this.time.start = moment()
          .subtract(1, "days")
          .startOf("day");
        this.time.end = moment().startOf("day");
        break;
      case 3:
        this.time.start = moment()
          .subtract(7, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      case 4:
        this.time.start = moment()
          .subtract(30, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      default:
        this.time.start = moment(this.SelTime[0]);
        this.time.end = moment(this.SelTime[1]);
        break;
    }
    this.currentPage = 1;
    this.Search();
  }

  // 选择删除
  handleSelectionChange(val) {
    this.multipleSelection = val;
  }
  // 删除
  DelCopy() {
    if (this.multipleSelection.length) {
      this.$confirm("此操作将删除改内容, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          const vid = this.multipleSelection.map(v => (v = v.vid));
          console.log(vid);

          // this.$message({
          //   type: "success",
          //   message: "删除成功!"
          // });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    } else {
      this.$message({
        type: "info",
        message: "没有选中"
      });
    }
  }
  // // 刷新重置
  // ResetAndreFresh(val) {
  //   switch (val) {
  //     case 0:
  //       this.time = {
  //         start: 0,
  //         end: moment()
  //       };
  //       this.getCopy();
  //       this.$message({ message: "重置成功！", type: "success" });
  //       break;
  //     case 1:
  //       this.Search();
  //       this.$message({ message: "刷新成功！", type: "success" });
  //       break;
  //     default:
  //       break;
  //   }
  // }

  // 下载报表
  download() {
    let time = {};
    time.start = moment(this.dowTime[0]);
    time.end = moment(this.dowTime[1]);

    this.$api
      .GteCopy({
        params: {
          pageSize: this.dowNum,
          currentPage: 1,
          search: this.dowUrl,
          time
        }
      })
      .then(async v => {
        let conf = {};
        conf.name = "CopyList";
        let alldata = [];
        for (let i = 0; i < v.data.rows.length; i++) {
          let arr = new Array();
          arr.push(
            moment(v.data.rows[i].created_at).format("YYYY-MM-DD, HH:mm:ss")
          );
          arr.push(v.data.rows[i].data.ip);
          arr.push(v.data.rows[i].data.regionName);
          arr.push(v.data.rows[i].data.city);
          arr.push(v.data.rows[i].data.search);
          arr.push(v.data.rows[i].data.url);
          arr.push(v.data.rows[i].data.wechar);
          arr.push(
            moment(v.data.rows[i].data.created_at).format(
              "YYYY-MM-DD, HH:mm:ss"
            )
          );
          alldata.push(arr);
        }
        conf.cols = [
          {
            caption: "复制时间",
            type: "string"
          },
          {
            caption: "ip",
            type: "string"
          },
          {
            caption: "省",
            type: "string"
          },
          {
            caption: "城市",
            type: "string"
          },
          {
            caption: "搜索词",
            type: "string"
          },
          {
            caption: "链接",
            type: "string"
          },
          {
            caption: "复制微信号",
            type: "string"
          },
          {
            caption: "打开页面时间",
            type: "string"
          }
        ];
        conf.rows = alldata;
        let result = excel.execute(conf);
        let data = new Buffer(result, "binary");
        result = new Blob([data], { type: "application/vnd.openxmlformats" });
        sever.saveAs(result, "报表.xlsx");
        this.dow = false;
      });
  }
}
</script>

<style scoped>
.time {
  margin: 0 15px;
}
.copy-funtion {
  display: flex;
  justify-content: space-between;
}
.user-list-utils-flex {
  display: flex;
  justify-content: space-between;
}
.el-card {
  margin-bottom: 20px;
  overflow-y: initial;
}
.el-card::-webkit-scrollbar {
  width: 5px;
}
.el-card::-webkit-scrollbar-thumb {
  background: #999;
  border-radius: 10px;
}
.copy-center {
  max-height: 570px;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table {
  position: relative;
}
.dowload {
  margin-right: 20px;
}
.time-select {
  margin-bottom: 20px;
}
</style>