<template>
  <el-row class="login">
    <el-col :xs="20" :sm="14" :md="10" :lg="7" :xl="5" class="login-box">
      <h2>统计后台</h2>
      <el-form class="demo-ruleForm" :model="user" :rules="rules" ref="user">
        <el-form-item prop="username">
          <el-input type="text" v-model="user.username" autocomplete="on" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" v-model="user.password" autocomplete="off" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login('user')" class="submit">登入</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import Vue from "vue";
import { mapActions } from "vuex"
import Component from "vue-class-component";
import jwtDecode from "jwt-decode";

@Component({
  methods:mapActions([
    "setUser","setLogin"
  ])
})
export default class login extends Vue {
  user = {};
  rules = {
    username: [{ required: true, message: "请输入账号！", trigger: "blur" }],
    password: [{ required: true, message: "请输入密码！", trigger: "blur" }]
  };
  // 登录
  login(name) {
    this.$refs[name].validate(valid => {
      if (valid) {
        this.$api.login(this.user).then(res => {
          const { code, message, token } = res.data;
          if (code) {
            this.$message({ message: message, type: "success" });
            localStorage.setItem('token', token);
            this.setUser(jwtDecode(token));
            this.setLogin(true);
            this.$router.push('/');
          } else {
            this.$message.error(message);
          }
        });
      } else {
        this.$message.error("请填写完整！");
        return false;
      }
    });
  }
}
</script>


<style scoped>
.login {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("../assets/images/login-bg.jpg") no-repeat;
  background-size: cover;
}
.login-box {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 330px;
  background: rgba(255, 255, 255, 0.4);
  padding: 40px 40px;
}
.login-box > h2 {
  font-size: 24px;
  padding-bottom: 20px;
  text-align: center;
}
.submit {
  width: 100%;
}
</style>