<template>
  <div>
    <el-card shadow="never" class="user-list">
      <h2 class="user-list-title">全部用户</h2>
      <span>（{{ Count }}）</span>
      <el-divider></el-divider>
      <el-table
        ref="SelectUser"
        :data="UserData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="SelectionChange"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>

        <el-table-column prop="username" width="300" align="center" label="账号"></el-table-column>

        <el-table-column label="姓名" align="center" width="300">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.profile.real_name?scope.row.profile.real_name:"无" }}</el-tag>
          </template>
        </el-table-column>

        <el-table-column label="手机号" align="center" width="300" prop="mobile"></el-table-column>

        <el-table-column prop="created_at" align="right" :formatter="formatter" label="账号添加时间"></el-table-column>
      </el-table>
    </el-card>

    <el-card shadow="never" class="user-list-utils">
      <div class="user-list-utils-flex">
        <div>
          <el-button type="primary" plain @click="AddUser()">添加用户</el-button>
          <el-button type="danger" plain @click="DelUser()" disabled>删除用户</el-button>
        </div>
        <div>
          <el-pagination layout="prev, pager, next" :total="Count"></el-pagination>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import Vue from "vue";
import Component from "vue-class-component";
import moment from "moment";

@Component
export default class UserList extends Vue {
  SelectUser = [];
  Count = 0;
  UserData = [];
  created() {
    this.getUserList();
  }
  // 获取用户列表
  getUserList() {
    this.$api.GetUser().then(res => {
      const { code, message, data } = res.data;
      if (code) {
        this.Count = data.count;
        this.UserData = data.rows;
      } else {
        this.$message.error("获取失败，请联系后端管理人员！");
      }
    });
  }
  // 格式化日期
  formatter(row, column) {
    return moment(row.created_at).format("YYYY-MM-DD");
  }
  // 跳转
  AddUser() {
    this.$router.push("/user/add")
  }
  // 删除用户
  // DelUser() {
  //   console.log(this.UserData)
  //   if (this.UserData) {
  //     this.$confirm("此操作将删除该用户, 是否继续?", "提示", {
  //       confirmButtonText: "确定",
  //       cancelButtonText: "取消",
  //       type: "warning"
  //     })
  //       .then(() => {
  //         this.$api.DelUser({ data: this.SelectUser }).then(data => {
  //           const { code, message } = data.data;
  //           if (code) {
  //             this.$message({ message: message, type: "success" });
  //             this.getUserList();
  //           } else {
  //             this.$message.error(message);
  //           }
  //         });
  //       })
  //       .catch(() => {
  //         this.$message({
  //           type: "info",
  //           message: "已取消删除"
  //         });
  //       });
  //   } else {
  //     this.$message({
  //       type: "info",
  //       message: "没有删除选项！"
  //     });
  //   }
  // }
  // 到底选择了谁？
  SelectionChange(val) {
    val.forEach((e, i) => {
      val[i] = e.id;
    });
    this.SelectUser = val;
  }
}
</script>

<style scoped>
.user-list {
  height: 700px;
  margin-bottom: 20px;
}
.user-list-title {
  font-size: 24px;
  float: left;
}
.user-list-title + span {
  padding-top: 7px;
  display: inline-block;
}
.check-all {
  margin-right: 30px !important;
}
.user-list-utils-flex {
  display: flex;
  justify-content: space-between;
}
</style>
