<template>
  <div>
    <el-row type="flex" justify="center">
      <el-col :xs="24" :sm="18" :md="14" :lg="12" :xl="10">
        <el-card class="box-card">
          <h2 class="add-user-title">添加用户</h2>
          <el-form
            :model="ruleForm"
            :on-success="handleAvatarSuccess"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
            :rules="rules"

          >
            <!-- <el-form-item label="用户头像">
              <el-upload
                class="avatar-uploader"
                action="/api/user/profile"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
                ref="upload"
                :auto-upload="false"
                :on-change="changePortrait"
                :headers="token"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item> -->
            <el-form-item label="登录名" prop="username">
              <el-input v-model="ruleForm.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPass">
              <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="状态" prop="disabled">
              <el-radio v-model="ruleForm.disabled" label="false">启用</el-radio>
              <el-radio v-model="ruleForm.disabled" label="true">禁用</el-radio>
            </el-form-item>
            <el-form-item label="手机号码" prop="mobile">
              <el-input v-model.number="ruleForm.mobile"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="ruleForm.email"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="real_name">
              <el-input v-model="ruleForm.real_name"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="gender">
              <el-radio v-model="ruleForm.gender" label="男">男</el-radio>
              <el-radio v-model="ruleForm.gender" label="女">女</el-radio>
              <el-radio v-model="ruleForm.gender" label="保密">保密</el-radio>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Vue from "vue";
import Component from "vue-class-component";
import moment from "moment";

@Component
export default class AddUser extends Vue {
  ruleForm = {
    username: "",
    password: "",
    checkPass: "",
    disabled: "false",
    email: "",
    mobile: "",
    real_name: "",
    gender: "男",
    portrait: ""
  };
  imageUrl = "";
  token = {
    Authorization: localStorage.token
  };
  rules = {
    username: [
      { required: true, message: "请输入用户名", trigger: "blur" },
      { min: 5, max: 11, message: "长度在 5 到 11 个字符", trigger: "blur" }
    ],
    password: [
      { required: true, message: "请输入密码", trigger: "blur" },
      { min: 5, max: 20, message: "长度在 5 到 20 个字符", trigger: "blur" }
    ],
    checkPass: [
      { validator: this.validatePass, trigger: "blur" },
      { required: true, message: "请确认密码", trigger: "blur" }
    ],
    mobile: [
      {
        pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
        message: "手机号格式不对",
        trigger: "blur"
      }
    ],
    email: [{ type: "email", message: "邮箱格式不对", trigger: "blur" }]
  };

  // 验证密码
  validatePass = (rule, value, callback) => {
    if (value == "") callback(new Error("请再次输入密码"));
    else if (value != this.ruleForm.password)
      callback(new Error("两次输入密码不一致!"));
    else callback();
  };

  submitForm(formName) {
    this.$refs[formName].validate(valid => {
      if (valid) {
        this.$refs.upload.submit();

        this.$api
          .PutUser({
            data: this.ruleForm
          })
          .then(v => {
            console.log(v.data);
          });
      } else {
        this.$message.error("输入错误，请检查！");
        return false;
      }
    });
  }
  resetForm(formName) {
    this.$refs[formName].resetFields();
  }

  //图片上传钩子
  handleAvatarSuccess(res, file) {
    if (res.ok) {
      this.ruleForm.portrait = res.portrait;
    }
  }
  beforeAvatarUpload(file) {
    const isJP = file.type === "image/jpeg" || file.type === "image/png";
    const isLt2M = file.size / 1024 / 1024 < 2;

    if (!isJP) {
      this.$message.error("上传头像图片只能是 JPG/PNG 格式!");
    }
    if (!isLt2M) {
      this.$message.error("上传头像图片大小不能超过 2MB!");
    }
    return isJP && isLt2M;
  }
  changePortrait(file) {
    this.imageUrl = URL.createObjectURL(file.raw);
  }
}
</script>

<style scoped>
.add-user-title {
  padding: 10px 20px;
  margin-bottom: 20px;
  font-size: 24px;
  border-bottom: #999 1px dashed;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
.avatar-uploader {
  line-height: 0;
}
</style>
