<template>
  <div class="home">
    <el-card shadow="hover">
      <el-button plain>今天</el-button>
      <el-button plain>昨日</el-button>
      <el-button plain>最近七天</el-button>
      <el-button plain>最近30天</el-button>

      <el-date-picker
        class="time"
        v-model="time"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
    </el-card>
    <el-card shadow="hover">
      <el-row class="browse" :gutter="20">
        <el-col :span="8">
          <el-card shadow="hover">从不显示</el-card>
          <el-card shadow="hover">从不显示</el-card>
          <el-card shadow="hover">从不显示</el-card>
          <el-card shadow="hover">从不显示</el-card>
        </el-col>
        <el-col :span="16">
          <div id="line"></div>
        </el-col>
      </el-row>
    </el-card>
    <el-card shadow="hover">
      <el-row class="region" :gutter="20">
        <el-col :span="6">
          <div id="map"></div>
        </el-col>
        <el-col :span="6">
          <div id="pie"></div>
        </el-col>
        <el-col :span="12">
          <el-scrollbar style="height:100%">
            <el-table
              :data="tableData"
              style="width: 100%"
              :default-sort="{prop: 'date', order: 'descending'}"
            >
              <el-table-column prop="regionName" label="地区" sortable  align="center" ></el-table-column>
              <el-table-column prop="count" label="点击量" sortable  align="center" ></el-table-column>
              <el-table-column prop="address" label="点击比率" sortable :formatter="formatter" align="center"></el-table-column>
            </el-table>
          </el-scrollbar>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
import Vue from "vue";
import Component from "vue-class-component";
import echarts from "echarts";
import "../../node_modules/echarts/map/js/china.js"; // 引入中国地图数据

@Component
export default class home extends Vue {
  time = "";
  count = 0;
  tableData = [];
  created() {
    this.getAddr();
  }
  getAddr() {
    this.$api.GetAddr().then(d => {
      const { code,count,data } = d.data
      this.count = count;
      this.tableData = data;
    });
  }
  formatter(row) {
    return ((row.count/this.count)*100).toFixed(2)+"%";
    
  }
  mounted() {
    this.EchartsLine();
    this.EchartsMap();
    this.EchartsPie();
  }
  EchartsLine() {
    let Line = echarts.init(document.getElementById("line"));
    let LineOption = {
      title: {
        text: "浏览次数",
        textStyle: {
          fontSize: 14
        },
        left: 30
      },
      color: ["#c62a2a", "#1bad7e"],
      tooltip: {
        trigger: "axis"
      },
      legend: {
        data: ["直接访问", "搜索引擎"]
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        boundaryGap: true,
        axisTick: {
          show: false
        },
        axisLine: {
          show: false
        },
        type: "category",
        data: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
        axisLabel: {
          show: true,
          textStyle: {
            color: "#999"
          }
        }
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        }
      },
      dataZoom: [
        {
          type: "inside",
          xAxisIndex: [0],
          filterMode: "filter",
          start: 0,
          end: 100
        }
      ],
      series: [
        {
          name: "直接访问",
          type: "line",
          stack: "总量",
          data: [320, 332, 301, 334, 390, 330, 320]
        },
        {
          name: "搜索引擎",
          type: "line",
          stack: "总量",
          data: [820, 932, 901, 934, 1290, 1330, 1320]
        }
      ]
    };
    Line.setOption(LineOption);
  }
  EchartsMap() {
    let Map = echarts.init(document.getElementById("map"));
    let MapOption = {
      title: {
        text: "来源统计",
        textStyle: {
          fontSize: 14
        },
        left: 30
      },
      tooltip: {}, // 鼠标移到图里面的浮动提示框
      visualMap: {
        type: "piecewise",
        splitNumber: 5,
        orient: "horizontal",
        pieces: [
          { min: 10000, color: "#117e61", label: "极高" },
          { min: 5000, max: 10000, color: "#20d19f", label: "高" },
          { min: 1000, max: 5000, color: "#33fbc3", label: "中等" },
          { min: 100, max: 1000, color: "#bffbec", label: "低" },
          { max: 100, color: "#e0fdf5", label: "极低" }
        ],
        text: ["高", "低"],
        left: "center"
      },
      geo: {
        // 这个是重点配置区
        map: "china", // 表示中国地图
        label: {
          normal: {
            show: false, // 是否显示对应地名
            textStyle: {
              color: "rgba(0,0,0,0.4)"
            }
          }
        },
        itemStyle: {
          normal: {
            areaColor: "#e3fcf5",
            borderColor: "#fff"
          },
          emphasis: {
            shadowBlur: 20,
            shadowColor: "#e3fcf5"
          }
        }
      },
      series: [
        {
          type: "scatter",
          coordinateSystem: "geo" // 对应上方配置
        },
        {
          name: "访问量", // 浮动框的标题
          type: "map",
          geoIndex: 0,
          data: [
            {
              name: "北京",
              value: 599
            },
            {
              name: "江西",
              value: 5909
            },
            {
              name: "上海",
              value: 142
            },
            {
              name: "黑龙江",
              value: 44
            },
            {
              name: "深圳",
              value: 92
            },
            {
              name: "湖北",
              value: 810
            },
            {
              name: "四川",
              value: 453
            }
          ]
        }
      ]
    };
    Map.setOption(MapOption);
  }
  EchartsPie() {
    let Pie = echarts.init(document.getElementById("pie"));
    let PieOption = {
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b}: {c} ({d}%)"
      },
      series: [
        {
          name: "访问量",
          type: "pie",
          radius: ["50%", "60%"],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: "center"
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: "18"
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [
            { value: 335, name: "直接访问" },
            { value: 310, name: "邮件营销" },
            { value: 234, name: "联盟广告" },
            { value: 135, name: "视频广告" },
            { value: 1548, name: "搜索引擎" }
          ]
        }
      ]
    };
    Pie.setOption(PieOption);
  }
}
</script>

<style scoped>
.time {
  margin: 0 15px;
}
.el-card {
  margin-bottom: 10px;
}
.browse > div {
  height: 290px;
}
.region > div {
  height: 320px;
}
#line,
#line > div,
#line > div > canvas {
  width: 100% !important;
  height: 100% !important;
}
#map,
#map > div,
#map > div > canvas,
#pie,
#pie > div,
#pie > div > canvas {
  height: 100% !important;
  width: 320px !important;
}
</style>