import axios from "axios";
import router from "../../router";
import { Loading,Message } from "element-ui";
import { ElLoadingComponent } from 'element-ui/types/loading';
import { throwErr } from "./throwErr"

const TIME_OUT_MAX  = 5000;
const URL = process.env.NODE_ENV === 'production' ? '' : '/'

const http = axios.create({
    baseURL:URL,
    timeout:TIME_OUT_MAX,
    responseType:"json",
    maxContentLength:4000,
    withCredentials: true,
})

let loading:ElLoadingComponent;

// 开始动画
function StatrLoading() {
    loading = Loading.service({
        lock:true,
        text:"正在读取数据...",
        background:'rgba(0,0,0,.7)'
    })
}

// 定义结束动画
function EndLoading() {
    loading.close();
}


// 请求拦截
http.interceptors.request.use(
    conf => {
        // 请求开始执行动画！
        StatrLoading();
        // 如果存在token则，给每个响应头加上token
        if(localStorage.token){
            conf.headers.Authorization = localStorage.token;
        }
        return conf;
    },
    // 如果请求错误则返回错误信息
    err => { 
        return Promise.reject(err);
        Message.error("请求超时！");
    } 
)
interface Res{
    code:number,
    msg:string
}
let res:Res;
// 响应拦截
http.interceptors.response.use(
    res => {
        // 结束动画
        EndLoading();
        return res;
    },
    err => {
        EndLoading();
        // 判断token是否失效
        if (err && err.response){
            const msg = throwErr(err.response.status,err.response);  
            Message.error({
                message:msg
            });
            // 清除token
            localStorage.removeItem('token');
            // 跳转到登录页面
            router.push('/login');
        }
        return Promise.reject(err);
    }
)

export default http;
