<template>
  <div class="home">
    <el-card shadow="never">
      <div class="copy-funtion">
        <div>
          <el-button plain @click="SearchTime(1)">今天</el-button>
          <el-button plain @click="SearchTime(2)">昨日</el-button>
          <el-button plain @click="SearchTime(3)">最近七天</el-button>
          <el-button plain @click="SearchTime(4)">最近30天</el-button>
          <el-date-picker
            class="time"
            v-model="selTime"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="SearchTime(5)"
          ></el-date-picker>
          <el-button plain @click="ResetAndreFresh(0)">重置</el-button>
          <!-- <el-button plain @click="ResetAndreFresh(1)">刷新</el-button> -->
        </div>
        <div>
          <el-input placeholder="请输入内容" v-model="search" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="Search()"></el-button>
          </el-input>
        </div>
      </div>
    </el-card>
    <el-card shadow="never" class="copy-center">
      <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{prop: 'date', order: 'descending'}"
        :row-class-name="tableRowClassName"
      >
        <el-table-column type="expand" width="55">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="访问来源">
                <span>：{{ props.row.url }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="url" align="center" label="访问来源" width="430"></el-table-column>
        <el-table-column
          prop="created_at"
          align="center"
          label="访问时间"
          sortable
          :formatter="formatterTime"
          width="180"
        ></el-table-column>
        <el-table-column prop="ip" align="center" label="访问IP"></el-table-column>
        <!-- :formatter="formatterAddress" -->
        <el-table-column prop="address" align="center" label="访问用户位置" ></el-table-column>
        <el-table-column prop="search" align="center" label="用户搜索词"></el-table-column>
        <el-table-column prop="wechar" align="center" label="访问微信"></el-table-column>
        <el-table-column prop="visitor_number" align="center" label="访问次数"></el-table-column>

        <el-table-column align="center" label="用户标签" width="400">
          <template slot-scope="props">
            <el-tag v-if="props.row.isiPad">iPad</el-tag>
            <el-tag v-else-if="props.row.iPhone">iPhone</el-tag>
            <el-tag v-else-if="props.row.isAndroidTablet">平板手机</el-tag>
            <el-tag v-else-if="props.row.isAndroid">Android</el-tag>

            <el-tag v-if="props.row.isWindows">Window</el-tag>
            <el-tag v-else-if="props.row.isLinux">Linux</el-tag>
            <el-tag v-else-if="props.row.isTablet">平板电脑</el-tag>
            <el-tag v-else-if="props.row.isMac">苹果电脑</el-tag>
            <!-- <el-tag type="warning">标签四</el-tag>
            <el-tag type="danger">标签五</el-tag>-->
            <!-- <el-tag type="success">成功复制</el-tag> -->
          </template>
        </el-table-column>

        <!-- <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>-->
      </el-table>
    </el-card>
    <el-card shadow="never" class="visit-list-utils">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
import Vue from "vue";
import Component from "vue-class-component";
import moment from "moment";

@Component
export default class visit extends Vue {
  time = {
    start: 0,
    end: moment()
  };
  selTime = []
  search = "";

  // 当前条数
  count = 0;
  // 数据
  tableData = [];
  // 当前一页大小
  pageSize = 10;
  // 在第几页
  currentPage = 1;

  created() {
    this.getVisitor();
  }

  // 获取表
  getVisitor(pageSize = 10, currentPage = 1, search = "", time) {
    this.$api
      .GetVisitor({
        params: {
          pageSize,
          currentPage,
          search,
          time,
        }
      })
      .then(v => {
        const { count, rows } = v.data;
        this.count = count;
        this.tableData = rows;
      });
  }
  // 过滤
  formatterTime(row) {
    return moment(row.created_at).format("YYYY-MM-DD, HH:mm:ss");
  }
  formatterAddress(row) {
    return  row.regionName + "" + row.city ;
  }
  // 页码
  handleSizeChange(val) {
    this.pageSize = val;
    this.getVisitor(val, this.currentPage, this.search, this.time);
  }
  handleCurrentChange(val) {
    this.currentPage = val;
    this.getVisitor(this.pageSize, val, this.search, this.time);
  }
  // 搜索词时间
  Search() {
    this.getVisitor(this.pageSize, this.currentPage, this.search, this.time);
  }
  SearchTime(val) {
    switch (val) {
      case 1:
        this.time.start = moment().startOf("day");
        this.time.end = moment();
        break;
      case 2:
        this.time.start = moment()
          .subtract(1, "days")
          .startOf("day");
        this.time.end = moment().startOf("day");
        break;
      case 3:
        this.time.start = moment()
          .subtract(7, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      case 4:
        this.time.start = moment()
          .subtract(30, "days")
          .startOf("day");
        this.time.end = moment();
        break;
      default:
        this.time.start = moment(this.selTime[0]);
        this.time.end = moment(this.selTime[1]);
        break;
    }
    this.currentPage = 1;
    this.Search();
  }

  // 刷新重置
  ResetAndreFresh(val) {
    switch (val) {
      case 0:
        this.time = {
          start: 0,
          end: moment()
        };
        this.getVisitor();
        this.$message({ message: "重置成功！", type: "success" });
        break;
      case 1:
        this.Search();
        this.$message({ message: "刷新成功！", type: "success" });
        break;
      default:
        break;
    }
  }

  tableRowClassName({ row, rowIndex }) {
    if (rowIndex === 1) {
      // return "warning-row";
    } else if (rowIndex === 3) {
      // return "success-row";
    }
    return "";
  }
}
</script>

<style scoped>
.time {
  margin: 0 15px;
}
.copy-funtion {
  display: flex;
  justify-content: space-between;
}
.el-card {
  margin-bottom: 20px;
  overflow-y: initial;
}
.el-card::-webkit-scrollbar {
  width: 5px;
}
.el-card::-webkit-scrollbar-thumb {
  background: #999;
  border-radius: 10px;
}
.copy-center {
  max-height: 570px;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>