<template>
  <div>
    <el-card shadow="never" class="u-utils">
      <el-button @click="drawer = true">新增链接</el-button>
    </el-card>
    <el-card shadow="never">
      <el-table :data="urlList" style="width: 100%">
        <el-table-column prop="url" label="我的链接" align="center"></el-table-column>
        <el-table-column prop="desc" label="备注" align="center"></el-table-column>

        <el-table-column label="状态" align="center">
          <template slot-scope="props">
            <el-switch
              v-model="props.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="启用"
              inactive-text="禁用"
              @change="status(props.row)"
              disabled
            ></el-switch>
          </template>
        </el-table-column>

        <el-table-column
          prop="created_at"
          label="创建时间"
          align="center"
          :formatter="formatterCreated_at"
        ></el-table-column>
        <el-table-column
          prop="updated_at"
          label="更新时间"
          align="center"
          :formatter="formatterUpdated_at"
        ></el-table-column>

        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)" disabled>编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-drawer :visible.sync="drawer" :with-header="false" class="drawer">
      <h2 class="drawer-title">添加链接</h2>
      <el-form class="demo-dynamic">
        <el-form-item
          v-for="(item, index) in UrlList"
          :key="index"
          :prop="item[index]"
          class="add-item"
        >
          <el-card shadow="never" class="card">
            <el-input v-model="item.url" placeholder="请输入链接" class="u-url"></el-input>
            <el-input v-model="item.desc" placeholder="请输入备注" class="u-desc"></el-input>
            <div class="u-status">
              <el-switch
                v-model="item.status"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="启用"
                inactive-text="禁用"
              ></el-switch>
              <el-button @click.prevent="removeUrl(item)">删除</el-button>
            </div>
          </el-card>
        </el-form-item>
        <el-form-item class="add-list">
          <el-button type="primary" @click="submitUrl">提交</el-button>
          <el-button @click="addUrl">新增链接</el-button>
        </el-form-item>
      </el-form>
    </el-drawer>

    <el-drawer :visible.sync="drawer_2" :with-header="false">
      <h2 class="drawer-title">修改链接</h2>
      <el-form class="demo-dynamic">
        <!-- <el-form-item>
          <el-input placeholder="请输入链接" class="u-url"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input placeholder="请输入备注" class="u-desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitUrl">提交</el-button>
        </el-form-item> -->
      </el-form>
    </el-drawer>
  </div>
</template>

<script>
import Vue from "vue";
import Component from "vue-class-component";
import { mapGetters } from "vuex";
import moment from "moment";

@Component({
  methods: mapGetters(["User"])
})
export default class MyUrl extends Vue {
  urlList = [];

  drawer = false;
  drawer_2 = false;

  UrlList = [{ url: "", desc: "", status: true }];

  created() {
    this.getUrlList();
  }
  getUrlList() {
    this.$api
      .GetUrlList({
        params: {
          uid: this.User().id
        }
      })
      .then(v => {
        const { code, message, data } = v.data;
        if (code) this.urlList = data;
      });
  }
  // 过滤器
  formatterCreated_at(row) {
    return moment(row.created_at).format("YYYY-MM-DD, HH:mm:ss");
  }
  formatterUpdated_at(row) {
    return moment(row.updated_at).format("YYYY-MM-DD, HH:mm:ss");
  }

  // 删除一个URL
  removeUrl(item) {
    var index = this.UrlList.indexOf(item);
    if (index !== -1) {
      this.UrlList.splice(index, 1);
    }
  }
  // 添加一个URL
  addUrl() {
    this.UrlList.push({
      url: "",
      desc: "",
      status: true
    });
  }
  // 提交URL
  submitUrl() {
    let data = this.UrlList.filter(v => {
      if (v.url) {
        return v;
      } else {
        return;
      }
    });
    if (data.length)
      this.$api.PutUrl({ data }).then(v => {
        const { code, message } = v.data;

        if (code) {
          this.$message({ message, type: "success" });
          this.drawer = false;
          this.getUrlList();
        } else this.$message.error(message);
      });
    else this.$message({ message: "没有添加的链接！", type: "warning" });
  }

  // 删除一个链接
  handleDelete(data) {
    this.$confirm("此操作将删除该链接, 是否继续?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$api.DelUrl({ data }).then(v => {
          const { code, message } = v.data;
          if (code) this.$message({ message, type: "success" });
          else this.$message.error(message);
          this.getUrlList();
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
  }

  // 修改链接
  handleEdit(row) {
    this.drawer_2 = true;
  }

  // 启用禁用
  status(row) {
    console.log(row);
  }
}
</script>

<style scoped>
.drawer-title {
  padding: 20px;
  font-size: 24px;
  border-bottom: 1px slateblue dotted;
}
.u-url,
.u-desc {
  width: 49%;
  margin: 20px 0;
}

.u-url {
  margin-right: 2%;
}
.u-status {
  display: flex;
  justify-content: space-between;
}
.u-utils {
  margin-bottom: 20px;
}

.el-card {
  border-top: 0;
}
.add-list {
  position: absolute;
  left: 20px;
  bottom: 20px;
}
.add-item {
  width: 100%;
}
.demo-dynamic {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-height: 750px;
  overflow-x: hidden;
}
.demo-dynamic::-webkit-scrollbar {
  width: 5px;
}
.demo-dynamic::-webkit-scrollbar-thumb {
  background: #999;
  border-radius: 10px;
}
</style>