import Vue from 'vue'
import VueRouter from 'vue-router'

// 单页
import Index from "@/views/Index.vue"
import Login from "@/views/Login.vue"
import Home from "@/views/Home.vue"

// 用户操作
import UserList from "@/views/user/UserList.vue"
import AddUser from "@/views/user/AddUser.vue"
import UpdataUser from "@/views/user/UpdataUser.vue"
import UserLog from "@/views/user/UserLog.vue"

// 角色列表
import RoleList from "@/views/role/RoleList.vue"
import AddRole from "@/views/role/AddRole.vue"
import UpdateRole from "@/views/role/UpdateRole.vue"

/**
 * 数据列表路由
 */
import CopyData from "@/views/data/CopyData.vue"
import VisitData from "@/views/data/VisitData.vue"
import Download from "@/views/data/Download.vue"


// 分类
import MyUrl from "@/views/classify/MyUrl.vue"

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'index',
    component:Index,
    children:[
      {
        path: '/',
        name: 'Home',
        component: Home,
      },
      {
        path:'/user/list',
        name:'UserList',
        component:UserList
      },
      {
        path:'/user/add',
        name:'AddUser',
        component:AddUser
      },
      {
        path:'/user/updata',
        name:'UpdataUser',
        component:UpdataUser
      },

      // 数据
      {
        path:'/data/copy',
        name:'CopyData',
        component:CopyData
      },
      {
        path:'/data/visit',
        name:'VisitData',
        component:VisitData
      },
      {
        path:'/data/download',
        name:'Download',
        component:Download
      },


      // 分类
      {
        path:'/classify/my-url',
        name:'MyUrl',
        component:MyUrl
      },

    ]
  },
  {
    path: '/login',
    name: 'login',
    component:Login
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  const isLogin = localStorage.token ? true : false;
  if(to.path === '/login'){
    next()
  }else{
    next()
    isLogin ? next() : next('/login');
  }
})
export default router
